.box {
    height: 100px;
    width: 100px;
    border-left: 1px solid black;
    border-top: 1px solid black;
    display: block;
    cursor: pointer;
  }

  @media screen and (max-width: 414px) {
    .box {
      height: 70px;
      width: 70px;
      display: block;
      cursor: pointer;
    }
  
  }