body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

.color {
  cursor: pointer;
  margin-top: 1rem;
  height: 100px;
  width: 100px;
  border: 1px solid black;
  margin-right: 0px;
  border-radius: 5px;
}

.colorSelector {
  display: grid;
  grid-template-columns: auto auto auto auto;
  margin: 2rem 40rem;
  margin-left: 42rem;
  background-color: rgb(173, 173, 173);
  padding: 1rem 1rem;
  padding-bottom: 2rem;
  padding-left: 3rem;
  border-radius: 20px;
}

.place {
  margin: 5rem 46.95rem;
  margin-left: 48rem;
  display: grid;
  align-self: center;
  grid-template-columns: auto auto auto auto;
  border-bottom: 1px solid black;
  border-right: 1px solid black;
}

@media screen and (max-width: 414px) {
  .place {
    margin: 3rem 5.3rem;
    margin-left: 3rem;
    display: grid;
    align-self: center;
    grid-template-columns: auto auto auto auto;;
  }
  .color {
    height: 50px;
    width: 50px;
    border: 1px solid black;
    margin-right: 0px;
    border-radius: 5px;
  }
  
  .colorSelector {
    display: grid;
    grid-template-columns: auto auto auto auto;
    margin: 2rem 2rem;
    margin-left: 2rem;
    margin-right: 5rem;
    background-color: rgb(173, 173, 173);
    padding: 1rem 1rem;
    padding-bottom: 2rem;
    padding-left: 2rem;
    border-radius: 20px;
  }
}